import React from 'react';
import PropTypes from 'prop-types';

import '../css/style.css';

const itemStyle = {
	flexShrink: 0,
	display: 'flex',
	flexDirection: 'column',
    alignItems: 'center'
};

const CatItem = ({ id, cat_name, img_src, text_color, img_color }) => (
    <div className="CatItem" style={itemStyle}>
		<div className="CatItemImg" style={{ background: img_color }}></div>
		<span className="CatItemText" style={{ color: text_color }}>{cat_name}</span>
	</div>
);
//<img src={img_src} alt={cat_name}/>
CatItem.propTypes = {
    id: PropTypes.string.isRequired,
    cat_name: PropTypes.string,
    img_src: PropTypes.string,
    text_color: PropTypes.string,
    img_color: PropTypes.string,
};

export default CatItem;