import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';


import Home from './panels/Home';
import Persik from './panels/Persik';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				case 'VKWebAppSetViewSettingsFailed':
					console.log(e.detail.data);
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		connect.send("VKWebAppSetViewSettings", {"status_bar_style": "dark", "action_bar_color": "#E533F9"});
	}

	/*if (connect.supports("VKWebAppSetViewSettings"))
		{
			connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#fff"});
			//connect.send("VKWebAppSetViewSettings", {"status_bar_style": "dark", "action_bar_color": "#fff"});
		}
		else
		{
			console.log("Unsupported");
		}*/
		//#AC0FBE
		//connect.send("VKWebAppSetViewSettings", {});
		//connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#fff"});
	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} />
				<Persik id="persik" go={this.go} />
			</View>
		);
	}
}

export default App;
