import React from 'react';
import PropTypes from 'prop-types';

import '../css/style.css';

const itemStyle = {
	flexShrink: 0,
	display: 'flex',
	flexDirection: 'column',
    alignItems: 'center'
};

const ActionSearch = ({ pop_name }) => (
    <div className="ActionItem" style={itemStyle}>{pop_name}</div>
);

ActionSearch.propTypes = {
    pop_name: PropTypes.string,
};

export default ActionSearch;